<template>
  <div class="container">
    <vue-markdown :source="source"></vue-markdown>
  </div>
</template>

<script>
  import axios from 'axios';
  import VueMarkdown from 'vue-markdown';

  export default {
    name: "Content",
    data: () => {
      return {
        source: ''
      }
    },
    components: {
      VueMarkdown
    },
    created: function () {
      axios.get('../' + this.$route.params.class + '/' + this.$route.params.article + '.md').then(resp => {
        this.source = resp.data;
      });
    }
  }
</script>

<style scoped>
  @import "../../public/css/content.css";
</style>