<template>
  <ul>
    <li v-for="article in articles">
      <router-link :to="{name: 'Content',params:{class:clazz,article:article.name}}">{{article.name}}</router-link>
    </li>
  </ul>
</template>

<script>
  import axios from 'axios';

  export default {
    name: "List",
    data: () => {
      return {
        clazz: '',
        articles: []
      }
    },
    created: function () {
      this.clazz = this.$route.params.class;
      axios.get('../computer/' + this.clazz + '.json').then(resp => {
        this.articles = resp.data;
      });
    }
  }
</script>

